export default {
    data(){
        return{
            dataUrl:'/api/gdekhback/special/goods_lists',
            list:[],
            goodsId:''
        }
    },
    mounted(){
        this.getgoodlist()
        if(window._czc){
            window._czc.push(["_trackEvent",'福利嗨翻天','进入','','','']);
        }else{
             setTimeout(function(){
               if( window._czc){
                  window._czc.push(["_trackEvent",'福利嗨翻天','进入','','','']);
              }
            }, 1000);
        }
    },
    methods:{
        toDetail(){
            if(this.goodsId){
                this.$router.push({path:'/details',query:{
                    id:this.goodsId
                }})
            }else{
                this.$vux.toast.show({
                    text: '请选择要购买的商品',
                    type: 'warn',
                    width: '10em'
                })
            }
        },
        getgoodlist(){
            this.$http.get(this.dataUrl).then((res) => {
                if(res.data.status == 0){
                    this.list = res.data.info;
                }
            })
        }
    },
    
}



